* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
  background-color: white;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  color: white;
  background-color: rgb(233, 188, 23);
  font-size: 2.5rem;
  margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: auto;
}

.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: white;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px black;
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px black;
  transform: scale(1.05,1.05);
}

 .card__img {
  width: 100%;
  height: 100%;
  max-height: 400px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;

}

.card__info {
  background-color: white;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
  background-color: white;
}

.card__cetegory {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: gray;
  background-color: white;
}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  background-color: white;
  text-transform: capitalize;
}


button {
  padding: 5px 8px ;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: gray;
  background-color: white;
  outline: none;
  border: 1px solid black;
  cursor: pointer;

}

